
:root {
  --bar-width: 100%;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*:before {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}
*:after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
