#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0 1rem;
}
.c-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.c-modal:before {
    content: "";
    position: absolute;
    background-color: #333;
    opacity: 0.4;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.c-modal--content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  background-color: white;
  padding: 1rem 2rem;
  border-radius: 20px;
}
.c-modal--content p {
    font-size: 2rem;
}
.c-modal--content button {
    font-size: 1rem;
}

.c-health {
  font-weight: bold;
  font-size: 1.125rem;
}
.c-health:before {
    display: table;
    content: " ";
}
.c-health:after {
    display: table;
    content: " ";
    clear: both;
}
.c-health .c-health--bar {
    position: relative;
    width: 100%;
    color: #272727;
    padding: 0.3125rem 0.625rem;
    margin-top: 0.5rem;
    border: 1px solid #272727;
}
.c-health .c-health--bar:before {
      content: "";
      width: var(--bar-width);
      height: 100%;
      background-color: #54b420;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
}
.c-health .c-health--player {
    float: left;
    width: 20%;
}
.c-health .c-health--dragon {
    float: right;
    width: 20%;
}
.c-health .c-health--dragon .c-health--bar:before {
      background-color: #b42020;
      width: var(--bar-width);
}

.c-controller {
  margin: 3rem auto;
}
.c-controller.is-disabled {
    opacity: 0.1;
    pointer-events: none;
}
.c-controller--btn {
  display: inline-block;
  width: 10%;
  font-weight: bold;
  padding: 0.3125rem 0;
  cursor: pointer;
  margin-left: 0.625rem;
}
.c-controller--btn:first-child {
    margin-left: 0;
}
.c-controller--btn__attack {
  background-color: #ED254E;
  color: white;
}
.c-controller--btn__blast {
  background-color: #efefef;
  color: white;
  cursor: not-allowed;
  pointer-events: none;
}
.c-controller--btn__blast.is-active {
    background-color: #F9DC5C;
    color: #272727;
    cursor: pointer;
    pointer-events: initial;
}
.c-controller--btn__heal {
  background-color: #efefef;
  color: white;
  cursor: not-allowed;
  pointer-events: none;
}
.c-controller--btn__heal.is-active {
    background-color: #C2EABD;
    color: #272727;
    cursor: pointer;
    pointer-events: initial;
}
.c-controller--btn__surrender {
  background-color: #011936;
  color: white;
}

